import request from '@/utils/request'

export function crawlData(data) {
  return request({
    url: '/v1/crawlData',
    method: 'post',
    data
  })
}

export function toXlsx(data) {
  return request({
    url: '/v1/toXlsx',
    method: 'get',
    params: data,
    responseType: 'arraybuffer' // 下载Excel必须设置此属性
  })
}
