<template>
  <div class="home">
    <el-form
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
    >
      <el-form-item label="Cookies" prop="cookies">
        <el-input
          type="textarea"
          v-model="ruleForm.cookies"
          :rows="4"
        ></el-input>
      </el-form-item>
      <el-form-item label="生成文件名" prop="fileName">
        <el-input v-model="ruleForm.fileName"></el-input>
      </el-form-item>
      <el-form-item label="起止时间" prop="time">
        <el-date-picker
          v-model="ruleForm.time"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          :loading="btnLoadingA"
          @click="submitForm('ruleForm')"
          >发起爬取请求</el-button
        >
        <el-button
          type="primary"
          :loading="btnLoadingB"
          @click="submitFormDown('ruleForm')"
          >发起下载请求</el-button
        >
        <el-button @click="resetForm('ruleForm')">重置表格</el-button>
        <el-button @click="openPlay">观看使用方法</el-button>
      </el-form-item>
    </el-form>

    <div class="play-iframe" v-if="show" @click="closePlay">
      <div class="play-box">
        <iframe
          src="//player.bilibili.com/player.html?bvid=BV1TA411B7Vg&page=1"
          width="1200"
          height="720"
          scrolling="no"
          border="0"
          frameborder="no"
          framespacing="0"
          allowfullscreen="true"
        >
        </iframe>
      </div>
    </div>
  </div>
</template>

<script>
import { crawlData, toXlsx } from '@/api'
export default {
  data() {
    return {
      show: false,
      btnLoadingA: false,
      btnLoadingB: false,
      ruleForm: {
        cookies: '',
        fileName: '',
        time: []
      },
      rules: {
        cookies: [
          { required: true, message: '请输入cookies', trigger: 'blur' }
        ],
        fileName: [
          { required: true, message: '请输入文件名称', trigger: 'blur' }
        ],
        time: [{ required: true, message: '请选择时间', trigger: 'blur' }]
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      }
    }
  },

  computed: {
    paramsFormat() {
      return {
        cookies: this.ruleForm.cookies,
        fileName: this.ruleForm.fileName,
        time: [
          `${this.ruleForm.time[0]} 00:00:00`,
          `${this.ruleForm.time[1]} 23:59:59`
        ]
      }
    }
  },

  methods: {
    // 爬取
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.crawlData(this.paramsFormat)
        } else {
          return false
        }
      })
    },

    // 下载
    submitFormDown(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.toXlsx(this.paramsFormat)
        } else {
          return false
        }
      })
    },

    openPlay() {
      this.show = true
    },

    closePlay() {
      this.show = false
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    // 请求爬取
    async crawlData(data) {
      try {
        this.btnLoadingA = true
        await crawlData(data)
        this.btnLoadingA = false
        this.$message({
          message: '请求成功，约10S后再下载表格！！！',
          type: 'success'
        })
      } catch (error) {
        this.btnLoadingA = false
      }
    },

    // Ajax请求表格
    async toXlsx(data) {
      try {
        this.btnLoadingB = true
        let res = await toXlsx(data)
        const blob = new Blob([res], {
          type: 'application/vnd.ms-excel'
        })
        const url = URL.createObjectURL(blob)
        const aLink = document.createElement('a')
        aLink.setAttribute('download', `${this.paramsFormat.fileName}.xlsx`)
        aLink.setAttribute('href', url)
        document.body.appendChild(aLink)
        aLink.click()
        document.body.removeChild(aLink)
        URL.revokeObjectURL(blob)
        this.btnLoadingB = false
        this.$message({
          message: '请求成功，表格下载完毕',
          type: 'success'
        })
      } catch (error) {
        this.btnLoadingB = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.home {
  width: 50%;
  margin: 150px auto;
  .play-iframe {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
    background-color: rgba(82, 81, 81, 0.4);
    .play-box {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 2;
    }
  }
}
</style>
